import React, { FC, ReactElement, useEffect, useState } from "react";
import { FC_GetSchoolsByDistrict, SchoolData } from "../../actions";
import { FC_GetDistricts } from "../../actions/transfer.action";
import Alert from "../../components/Alert/Alert";
import Button from "../../components/Buttons/Button";
import InputSelect from "../../components/Inputs/InputSelect";
import SearchInput from "../../components/Inputs/SearchInput";
import TextInput from "../../components/Inputs/TextInput";
import Loading from "../../components/Loading/Loading";
import Modal, { ModalSize } from "../../components/Modal/Modal";
import { DistrictTypes } from "../../types/TransferTypes";
import { search } from "../../utils/functions";
import {
  FC_Search_Available_Candidates,
  FC_SubmitSchoolToTraining,
  InterfaceTrainingRequestSchools,
  TrainingInterface,
} from "../actions/training.settings.action";

type AddSchoolProps = {
  successHandler: (data: InterfaceTrainingRequestSchools) => void;
  close: () => void;
  training: TrainingInterface;
  training_schools: InterfaceTrainingRequestSchools[];
  remaining: number;
};

interface AlertInterface {
  target: "MAIN-ERROR" | "MAIN-SUCCESS" | null;
  msg: string;
}

const defaultAlert: AlertInterface = {
  target: null,
  msg: "",
};

const AddSchool: FC<AddSchoolProps> = ({
  successHandler,
  close,
  training,
  training_schools,
  remaining,
}): ReactElement => {
  const [error, setError] = useState<AlertInterface>(defaultAlert);
  const [districts, setDistricts] = useState<DistrictTypes[]>([]);
  const [selected_district, setSelectedDistrict] = useState<string>("");
  const [search_result, setSearchResults] = useState<string>("");
  const [loading_schools, setLoadingSchools] = useState<boolean>(false);
  const [selected_number, setSelectedNumber] = useState<number>(0);
  const [schools_list, setSchoolsList] = useState<SchoolData[]>([]);
  const [selectedSchool, setSelectedSchool] = useState<SchoolData | null>(null);

  const [loadingCheckAvaliable, setLoadingCheckAvailable] =
    useState<boolean>(false);
  const [availableTeachers, setAvailableTeachers] = useState<number>(0);
  const [loadingSubmitSchool, setLoadingSubmitSchool] =
    useState<boolean>(false);

  useEffect(() => {
    if (
      training.trainee_criteria?.districts &&
      training.trainee_criteria?.districts.length >= 1
    ) {
      setDistricts(
        training.trainee_criteria?.districts.map((el) => ({
          district_code: el.id + "",
          district_name: el.name,
        }))
      );
    } else {
      FC_GetDistricts((status, data, err) => {
        if (data) {
          setDistricts(data);
          setError(defaultAlert);
        } else {
          setError({
            target: "MAIN-ERROR",
            msg: `Error: ${err}`,
          });
        }
        clearError();
      });
    }
  }, [training]);

  const getDistrictSchools = (e: React.ChangeEvent<HTMLSelectElement>) => {
    let value = e.target.value;
    setSelectedDistrict(value);
    if (value === "") return;

    setLoadingSchools(true);
    FC_GetSchoolsByDistrict(value, (status, data, err) => {
      if (status && data) {
        setSchoolsList(data);
      } else {
        setError({
          target: "MAIN-ERROR",
          msg: `Error: ${err}`,
        });
      }
      setLoadingSchools(false);
    });
  };

  const selectSchoolCheckAvailable = (school: SchoolData | null) => {
    console.log({ submitt_school: school });

    if (!training) return;
    setAvailableTeachers(0);
    if (!school) return setSelectedSchool(null);
    setSelectedSchool(school);
    setLoadingCheckAvailable(true);
    FC_Search_Available_Candidates(
      {
        criteria: training.trainee_criteria,
        end_date: training.end_date + "",
        start_date: training.start_date + "",
        school_code: school.school_code,
        sub_area_id: training.sub_area_id + "",
        topic: training.topic_id + "",
        training_id: training.id + "",
      },
      (status, data, err) => {
        if (!status)
          setError({
            msg: `Error: ${err}`,
            target: "MAIN-ERROR",
          });
        clearError();
        setLoadingCheckAvailable(false);
        if (data) setAvailableTeachers(data.total);
      }
    );
  };

  const clearError = () =>
    setTimeout(() => {
      setError(defaultAlert);
    }, 3000);

  const submitTotalNumber = () => {
    if (!selectedSchool) return;
    if (availableTeachers < selected_number) {
      setError({
        target: "MAIN-ERROR",
        msg: "the number of teacher you are submitting is higher than tha available teachers on this school",
      });
      return;
    }

    setLoadingSubmitSchool(true);
    //* Submit the data
    FC_SubmitSchoolToTraining(
      {
        training_id: training.id,
        school_id: selectedSchool.school_code,
        district_id: null,
        sector_id: null,
        institution_id: null,
        total: selected_number,
      },
      (status, msg) => {
        setLoadingSubmitSchool(false);

        if (status) {
          successHandler({
            approved_by: "",
            approved_date: "",
            available_trainee: 0,
            district_id: selected_district,
            district_name: "",
            group_id: "",
            id: "",
            institution_id: training.development_partner_id + "",
            school_code: selectedSchool?.school_code,
            school_id: selectedSchool?.school_id,
            school_name: selectedSchool?.school_name,
            sector_id: "",
            total: selected_number,
            training_id: training.id,
          });
          setError({
            target: "MAIN-SUCCESS",
            msg: "The school submitted successfully",
          });
        } else {
          setError({ msg: `Error: ${msg}`, target: "MAIN-ERROR" });
        }
        clearError();
        setTimeout(() => {
          close();
        }, 3000);
      }
    );
  };

  let TeachersResults: SchoolData[] = search(schools_list, search_result, {
    school_code: true,
    school_name: true,
  });

  /* function body */
  return (
    <Modal
      backDrop={true}
      close={close}
      backDropClose={false}
      displayClose={true}
      widthSizeClass={ModalSize.extraLarge}
      padding={{
        title: true,
        body: undefined,
        footer: undefined,
      }}
      title="Add school"
    >
      <div className="border-t p-2">
        {error.target === "MAIN-SUCCESS" && (
          <Alert theme="success" fixed>
            {error.msg}
          </Alert>
        )}
        {error.target === "MAIN-ERROR" && (
          <Alert theme="danger" fixed>
            {error.msg}
          </Alert>
        )}

        {selectedSchool !== null ? (
          <>
            <div>
              <div className="border p-2 rounded items-center flex">
                <section>
                  <Button
                    type="button"
                    theme="default"
                    onClick={() => setSelectedSchool(null)}
                  >
                    Back
                  </Button>
                </section>
                <section className="pl-4 flex-1">
                  <label className="text-gray-400 text-sm">
                    Selected school
                  </label>
                  <h2 className="font-bold text-lg">
                    {selectedSchool.school_code} {selectedSchool.school_name}
                  </h2>
                </section>
              </div>
              <div className="p-2">
                {loadingCheckAvaliable && (
                  <div className="py-4 text-center">
                    <section className="inline-block">
                      <Loading />
                    </section>
                  </div>
                )}
                <div className="my-2 p-2 rounded border flex-- items-center">
                  <div className="flex-1">
                    <label className="text-sm text-gray-600">
                      Total candidates available
                    </label>
                    <h3 className="text-2xl font-bold">{availableTeachers}</h3>
                  </div>
                </div>{" "}
                <section className="flex-1">
                  <TextInput
                    onChange={(e) => setSelectedNumber(+e.target.value)}
                    value={selected_number}
                    type="number"
                    error={""}
                    label={"Total trainee needed on this school"}
                    placeholder={""}
                    name={""}
                    max={availableTeachers}
                  />
                </section>
                <section className="text-right">
                  <Button
                    size="lg"
                    type="button"
                    loading={loadingSubmitSchool}
                    onClick={submitTotalNumber}
                  >
                    Add school to the list
                  </Button>
                </section>
              </div>
            </div>
          </>
        ) : (
          <>
            <div className="p-2 border m-1 rounded flex items-center gap-2">
              <section>
                <InputSelect
                  onChange={getDistrictSchools}
                  value={selected_district}
                  error={""}
                  label={""}
                  placeholder={""}
                  name={""}
                  data={[
                    { title: "Select a district", value: "" },
                    ...districts.map((el) => ({
                      title: el.district_name,
                      value: el.district_code,
                    })),
                  ]}
                />
              </section>
              <section className="flex-1">
                <SearchInput
                  onChange={(e) => setSearchResults(e.target.value)}
                  size="sm"
                  value={search_result}
                  placeholder={"Search for the school name or school code"}
                  name={""}
                />
              </section>
            </div>

            {selected_district === "" ? (
              <div className="text-center py-6">
                <h2 className="text-2xl pb-1 text-gray-600">
                  Please select a district
                </h2>
                <p>You must select a district in order to see some results</p>
              </div>
            ) : (
              <div className="pt-2 mt-2 border-t">
                {loading_schools && (
                  <div className="py-8 text-center">
                    <section className="inline-block">
                      <Loading />
                    </section>
                  </div>
                )}{" "}
                {!loading_schools && schools_list.length <= 0 && (
                  <div className="py-4 text-center">
                    <h3 className="text-2xl text-gray-600">No results found</h3>
                  </div>
                )}
                {!loading_schools && TeachersResults.length <= 0 && (
                  <div className="h-80vh overflow-auto">
                    <div className="py-4 text-center">
                      <h3 className="text-2xl text-gray-600">
                        No results found
                      </h3>
                    </div>
                  </div>
                )}
                {TeachersResults.length >= 1 && schools_list.length >= 1 && (
                  <div className="h-80vh overflow-auto">
                    <table className="w-full">
                      <thead>
                        <tr>
                          <th className="border p-2">School code</th>
                          <th className="border p-2">School name</th>
                          <th className="border p-2">School Category</th>
                          <th className="border p-2">School status</th>
                          <th className="border p-2">##</th>
                        </tr>
                      </thead>
                      <tbody>
                        {TeachersResults.map((el, i) => {
                          let schoolExist = training_schools.find(
                            (itm) => itm.school_code === el.school_code
                          );
                          return (
                            <tr key={i}>
                              <td className="p-2 border">{el.school_code}</td>
                              <td className="p-2 border">{el.school_name}</td>
                              <td className="p-2 border">
                                {el.school_category}
                              </td>
                              <td className="p-2 border">{el.school_status}</td>
                              <td className="p-2 border w-5">
                                {!schoolExist && (
                                  <Button
                                    size="sm"
                                    type="button"
                                    theme="accent-light"
                                    onClick={() =>
                                      selectSchoolCheckAvailable(el)
                                    }
                                  >
                                    Select
                                  </Button>
                                )}
                              </td>
                            </tr>
                          );
                        })}
                      </tbody>
                    </table>
                  </div>
                )}
              </div>
            )}
          </>
        )}
      </div>
    </Modal>
  );
};
export default AddSchool;
